.mainDiv {
    position: relative;
    height: 1000px;
}

.navbar-toggler{
    width: 47px;
    height: 34px;
    background-color: transparent;
    margin-top: 3%;
}

.navbar-toggler .line{
    width: 100%;
    float: left;
    height: 2px;
    background-color: #fff;
    margin-bottom: 5px;
}

button:focus {
    outline: 0px;
}

.navbar {
    background-color: transparent;
}


.nav-link {
    margin-right: 40px;
}


.buttonX {
    display: none;
    z-index: 10000;
    color: white;
    position: absolute;
    right: 6%;
    top: 5%;
    cursor: pointer;
}






@media (max-width:767px) {
    .mainDiv {
        height: 2000px;
    }


    .nav-link {
        margin-top: 3%;
        margin-right: 10px;
    }

    #nav1>div>.nav-link {
        position: absolute;
        color: #008956;
        top: 32%;
        left: 43%;
        padding-left: 0.1vw;
        padding-right: 0.1vw;
        line-height: 10%;
        font-size: 5vw;
        font-family: Mont;
        margin-right: 2%;
        z-index: 10000;
        margin-top: 0%;
    }
    
    
    #nav1>div>.hell1 {
        cursor: pointer;
    }
    
    
      
    #nav1>div>.hell2 {
        top: 40%;
    }
    
    #nav1>div>.hell3 {
        top: 48%;
    }
    #nav1>div>.hell4 {
        top: 56%;
        cursor: pointer;
    }
    #nav1>div>.hell5 {
        top: 64%;
    }
    
    .navbar-nav {
        position: fixed;
        right: 0;
        bottom: 0;
        width: 0px;
        height: calc(100vh);
        z-index: 5000;
        background: rgb(0, 0, 12);
        background-size: 400% 400%;
        animation: change 5s ease-in-out infinite;
        transition: all 0.3s ease-in-out;
        visibility: hidden;
    }
    
}